@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap);
body {
  font-family: 'Noto Sans KR', sans-serif;

  background-color: red; /* Old browsers */
  text-align: center;

}
.App {
	font-family: 'Noto Sans KR', sans-serif;
	height: 100vh;
	width: 100vw;
	text-align: center;
	background-color: #65799B;
	background-repeat: no-repeat;
}
.main {
	height: 100%;
	width: 100%;
}
.LogoBox {
	width: 100%;
}
.Logo {
	margin-top: 150px;
	width: 200px;
}

.Description {
	color: white;
	font-size: 18px;
	margin-bottom: 70px;
	margin-top: 30px;
	width: 100%;
}
.SearchBox {
	width: 100%;
}
.SearchBar {
	margin-top: 50px;
	width: 100%;
}
.About {
	margin-bottom: 100px;
}
.CardBox {
	background-color: white;
	border: medium solid lightgrey;
	border-radius: 10px 10px 10px 10px;
	font-family: 'Noto Sans KR', sans-serif;
	margin-bottom: 5px;
	margin-top: 5px;
	padding: 10px;
	word-break: break-all;
}
.highlight {
	color: #FF0000;
	font-weight: bold;
}
.SearchApp {
    display: -webkit-flex;
    display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
    font-family: 'Noto Sans KR', sans-serif;

    background-color: #65799B; /* Old browsers */
   
    width: 100%;
    height: 100%;
}
.LeftSide {
	text-align: center;
	width: 100%;
}
.MiddleSide {
	background-color: white;
}
.SubSearchBar {
	margin-bottom: 30px;
	margin-left: 16px;
	margin-right: 16px;
	margin-top: 30px;
	text-align: left;
}
.SubLogo {
	height: 40px;
	margin-bottom: 15px;
	margin-top: 15px;
}
.ResultBox {
	width: 100%;
	border-top: 1px solid lightgray;
	padding-bottom: 10px;
	padding-top: 10px;
	text-align: left;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}
.NotDataView {
	height: 100%;
}
